.c-vertical-nav {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 80px 1fr 40px;
  max-height: 100vh;
  &__header {
    height: 80px;
    display: flex;
    align-items: center;
    padding: 16px; }
  &__body {
    padding: 16px;
    height: 100%;
    overflow: auto; }
  &__list {
    list-style: none;
    padding: 0;
    margin: 0;
    padding-top: 8px;
    &-item {
      padding: 8px 16px;
      cursor: pointer;
      display: grid;
      grid-template-columns: 1fr 16px;
      align-items: center;
      border-radius: 8px;
      &__close {
        display: none;
        background-color: #e3e3e4;
        width: 18px;
        height: 18px;
        border-radius: 8px;
        color: #285488;
        padding: 1px; }
      &:hover, &--active {
        background-color: #285488; }
      &:hover {
        .c-vertical-nav__list-item__close {
          display: flex; } } } }

  &__footer {
    display: grid;
    padding: 8px;
    height: 40px; } }
